.app-wrapper{
  display: grid;
  width: 1200px;  
  margin: 0 auto;
  grid-template-columns: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  grid-template-areas: 
  'header header'
  'nav content';

}

.app-wrapper-content{
    grid-area: content;
    background-color: lightblue;
}

